// **** login, register **** //



.login-page-wrapper,
.register-page-wrapper {
  display: flex;
  min-height: 100vh;
  width: 100vw;
  align-items: top;
  justify-content: center;
  padding-top: 100px;
  // background-image: url(https://www.gardeningknowhow.com/wp-content/uploads/2021/03/foliage.jpg);
  background-repeat: no-repeat;
  background-size: cover;
}

.register-page-wrapper {
  padding-top: 20px;
}

// Form Page
.form-page {
  width: 100%;
  text-align: center;
  margin-top: 0;

  .btn-main {
    // background-color: darken(#467A43, 15%);
    color: white;
    width: 33%;
    border-radius: 5px;
    height: 2em;
    margin-top: 1em;

    &:hover {
      // background-color: darken(#467A43, 20%);

      transform: scale(1.1);

    }
  }

  h1 {
    font-size: 40px;
    margin-bottom: 20px;
    color: white;
  }

  form {
    background: darken($primary, 15);
    padding: 40px;
    border-radius: 18px;
    width: 50%;
    display: inline-block;
    border: 2px solid $primary;
    // box-shadow: 5px 5px #467A43;
    margin-top: 10px;
    padding-top: 10px;

    label {
      font-size: 0.9rem;
      color: white;
      margin-bottom: 10px;
    }

    input {
      width: 100%;
      margin-bottom: 10px;
      border: none;
      padding: 5px;
      margin-top: 10px;
    }

    small {
      display: block;
      margin-bottom: 15px;
    }
  }
}

.login-page-wrapper {
  padding-top: 15px;
}

.image-upload-div {
  margin-top: 20px;
  color: white;
}

.post-cat {
  padding-top: 0;
  margin-top: 0;

  p {
    color: white;
  }
}

.btn-form {
  background-color: white;
  color: grey;
  width: 25%;
  border-radius: 5px;
  height: 3em;
  margin-top: 1em;
  border: none;

  &:hover {
    transform: scale(1.1);
  }
}

