  justify-content: center;
  width: 300px;
  margin: 10px;
  border: 7px solid $primary;
  // border-radius: 10px;
  text-decoration: none;
}

.profile-row {
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
  margin: 0 15px;
  align-items: center;
  // border: none;
}

.profile-card-image {
  height: 19vh;
  background-size: contain;
  background-position: center;
